@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap);

* {
  box-sizing: border-box;
}

body {
  background-color: #1233ff;
}


ul { 
  padding-left: 10px;
}

.background-white {
  background-color: white;
  color: #1233ff;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: bold;
  margin-left: 200px;
}

.nav-main {
  display: grid;
  grid-template-columns: 20px 1fr;
  grid-column-gap: 2px;
  width: 90%;
  margin: 0 auto;
}

aside h3, aside ul{
  font-family: 'Poppins', sans-serif;
  color: white;
  padding: 0;
}

.nav h3 {
  font-weight: bold;
  font-size: 26px;
}

.add-state label {
  font-size: 20px;
}

.add-state ul {
  margin: 0;
}

.state-title {
  font-size: 22px;
  border-radius: 15px 15px 0 0;
  padding: 3px 7px;
  /* text-align: center; */
  font-family: 'Poppins', sans-serif;
  background-color: white;
  color: #1233ff;
  margin: 0;
  padding-bottom: 0;
}

.gray-bgc {
  background-color: lightgray;
  padding: 0 5px;
}

.field-title {
  border: #1233ff 1px solid;
  border-width: 2px 0;
  font-size: 20px;
  padding: 3px 7px;
  
}



.field-card {
  margin: 0;
}

.nav {
  width: 200px;
  display: inline;
  float: left;
}

.content {
  margin: 5px;
  font-size: 18px;
  width: 80%;
  display: inline;
  margin-left: 200px;
  /* float: right; */
}

.arrows {
  display: float;
  float: right;
  width: 20px;
  height: 20px;
  margin: 3px 3px;
}

.arrows:hover, .button-looks label:hover {
  cursor: pointer;
}

.big-title {
  display: inline-block;
  margin: 10px 15px;
}

.state-info {
  padding: 0 5px;
}

.label-arrows {
  display: inline-block;
  text-align: right;
}

.title-float-right {
  display: grid;
  /* float: right; */
  margin: 4px;
}

.grid-for-buttons {
  display: block;
  /* grid-template-columns: 1fr 1fr 1fr; */
}

.share-print-save {
  width: 90px;
  height: 50px;
  display: inline-block;
}
.all {
  width: 125px;
  height: 50px;
  float: right;
}

.all-icon {
  width: 18px;
  height: 17px;
  padding-top: 5px;
  margin-top: 2.5px;
}

.share-icon .all-icon {
  float: left;
}

.share-label {
  margin-top: 5px;
  float: right;
}

.Sticky_sticky__3nOwO {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    /* padding: 1.5rem; */
    /* background-color: rebeccapurple; */
    color: white;
    margin: 2px 0;
  }
  
  
  .Sticky_sticky__section__N4RjL {
    position: relative;
  }
  
  .Sticky_sticky__sentinel_top__Up_mG {
    background: yellowgreen;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
  
    display: flex; align-items: center; justify-content: center;
    visibility: hidden;
  }
  
  .Sticky_sticky__sentinel_bottom__2DNL3 {
    z-index: 1;
    background: rgba(255, 255, 0, 0.253);
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
  
    display: flex; align-items: center; justify-content: center;
    visibility: hidden;
  }
  
