.header-grid {
  display: grid;
  grid-template-columns: repeat(2, 40px) 55px minmax(30px, 90px) minmax(30px, 165px) minmax(30px, 60px) minmax(30px, 115px) minmax(30px, 100px) minmax(30px, 65px) minmax(30px, 165px) ;
  float: right;

  padding-top: 10px;
}
header .links{
  color: white;
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
  font-size: 15px;
  font-weight: 900;
  text-shadow: 0.75px 0 #ffffff;
  padding-top: 10px;
}

header a {
  padding-top: 5px;
}

header .login {
  color:#9dff88;
  text-decoration: underline;
}


.banner {
  display: block;
  margin: 15px 0;
  width: 100%;
}

header .button-looks {
  display: block;
  float: right;
}

.button-looks {
  background-color: #9dff88;
  border: none;
  color: #1233ff;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  
  font-size: 15px;
  margin: 4px 2px;
  cursor: pointer;
  
  font-weight: 900;
  text-shadow: 1px 0 #1233ff;
  border-radius: 5px;
}

footer * {
  font-family: 'Source Sans Pro', sans-serif;
  color: white;
}

footer {
  width: 80%;
  margin: 0 auto;
}

footer a {
    color: #9dff87;
}

.footer-grid {
  display: grid;
  grid-template-columns: minmax(200px, 1fr) minmax(650px, 1fr);
  font-size: 14px;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}
